@media screen  and (min-width: 768px) {
    #header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-bottom: 2px solid #5E17EB;
        height: 60px;
        background-color: #171515;
    }

    #header:hover {
        -webkit-text-stroke: 0.3px;
    }
    
    .container {
        display: flex;
        justify-content: space-between;
    }
    
    .routes-container {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 270px;
        margin-right: 26px;
        font-family: Arial, Helvetica, sans-serif;
        color: white;    
    }
    
    .icon-image {
        width: 26px;
        height: 26px;
        align-self: center;
        margin-left: 26px;
    }

    .nav-btn-div {
        display: none;
    }
    
    /* Para criar esse efeito utilizei essa referÃªncia https://dev.to/afif/100-underline-overlay-animation-the-ultimate-css-collection-4p40 */
    .nav-menu-option {
        background: 
            linear-gradient(#5E17EB 0 0) 
            bottom /var(--d, 0) 2px 
            no-repeat;
        transition:0.5s;
      }

    .nav-menu-option:hover {
        --d: 100%;
    }

    .nav-menu-option {
        text-decoration: none;
        color: white;
    }
    
}


@media screen and (max-width: 768px) {
    #header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        border-bottom: 2px solid #5E17EB;
        background-color: #171515;
        margin-top: -16px;
        height: 70px;
    }

    .container {
        display: flex;
        justify-content: space-between;
        margin-top: 20px;
    }

    .title-container {
        align-self: center;
    }

    .routes-container {
        display: none;
    }

    .icon-image {
        width: 26px;
        height: 26px;
        align-self: center;
        margin-left: 26px;
    }

    .block-nav-bar {
        align-items: center;
        border: solid 2px #5E17EB;
        border-top: none;
        color: white;
        background-color: #171515;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        width: 290px;
        height: 40px;
        margin-left: -243px;
        position: absolute;
        font-family: Arial, Helvetica, sans-serif;
        z-index: 1;

    }
    
    .nav-btn {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 30px;
    }

    .nav-btn-div {
        align-self: center;

    }

    .nav-img {
        width: 49px;
        height: 49px;
    }

    .nav-img:active {
        box-shadow: -3px -3px 10px #5E17EB;
        border-radius: 50%;
    }

    .nav-menu-option:active {
        padding: 4px;
        box-shadow: 2px 2px 10px #5E17EB;
    }

    .nav-menu-option {
        text-decoration: none;
        color: white;
    }
}